<template>
    <div class="capabilities-main">
        <div class="header">
            <div class="header-bar">
                <img src="../assets/imgs/logo-red.svg" alt="LOGO">
                <div class="header-nav">
                    <div class="menu-item">
                        <router-link to="/projects"> Work Experience </router-link>
                    </div>
                    <div :md="12" class="menu-item selected">
                        <router-link to="/capabilities"> Professional Skills </router-link>
                    </div>
                </div>
            </div>
        </div>
        <div class="content">

            <!-- 图标数据 -->
            <div class="chart-set">
                <div id="chart-content">
                </div>

            </div>
            <div class="show-type tag-labels">
                <span :class="{ seleted: currentRow == 'framework' }" @click="changeRow('framework')">FrameWork</span>
                <span :class="{ seleted: currentRow == 'language' }" @click="changeRow('language')">Language</span>
            </div>
            <!-- 表格数据 -->
            <div class="table-set">

            </div>
        </div>
    </div>
</template>
<script>
// 引入 echarts 核心模块，核心模块提供了 echarts 使用必须要的接口。
import * as echarts from 'echarts/core';
// 引入柱状图图表，图表后缀都为 Chart
import { BarChart } from 'echarts/charts';
// 引入提示框，标题，直角坐标系，数据集，内置数据转换器组件，组件后缀都为 Component
import {
    TitleComponent,
    TooltipComponent,
    GridComponent,
    DatasetComponent,
    TransformComponent
} from 'echarts/components';
// 标签自动布局，全局过渡动画等特性
import { LabelLayout, UniversalTransition } from 'echarts/features';

// 引入 Canvas 渲染器，注意引入 CanvasRenderer 或者 SVGRenderer 是必须的一步
import { CanvasRenderer } from 'echarts/renderers';

// 注册必须的组件
echarts.use([
    TitleComponent,
    TooltipComponent,
    GridComponent,
    TransformComponent,
    DatasetComponent,
    BarChart,
    LabelLayout,
    UniversalTransition,
    CanvasRenderer
]);

import axios from 'axios';

export default {
    name: "CapabilitiesPage",
    created: async function () {
        let data = await axios.get('/data/projectTech.json');
        this.calcaluteData(data);
    },
    data: function () {
        return {
            currentRow: "framework",
            projectTechs: {
                framework: [],
                language: []
            },
            options: {
                title: {
                    text: 'FRAMEWORK',
                    top: '10',
                },
                dataset: [
                    {
                        dimensions: ['name', 'score'],
                        source: [
                        ]
                    },
                    {
                        transform: {
                            type: 'sort',
                            config: { dimension: 'score', order: 'asc' }
                        }
                    }
                ],
                grid: {
                    show: false,
                    containLabel: false,
                    borderWidth: 10,
                    left: '50%',
                },
                xAxis: {
                    type: 'value',
                    splitLine: {
                        show: false
                    },
                    axisLabel: {
                        show: false
                    }
                },
                yAxis: {
                    type: 'category',
                    axisLabel: {
                        rotate: 0
                    },
                    axisTick: {
                        show: false
                    }
                },
                series: {
                    type: 'bar',
                    encode: { x: 'score', y: 'name' },
                    datasetIndex: 1
                },
                color: ["#606266"],
                barMaxWidth: 55
            }
        }
    },
    mounted: function () {
        // 接下来的使用就跟之前一样，初始化图表，设置配置项
        var myChart = echarts.init(document.getElementById('chart-content'));
        myChart.setOption(this.options);

        //resize
        window.addEventListener('resize', this.chartResize);
        if (window.history && window.history.pushState) {
            history.pushState(null, null, document.URL);

            window.addEventListener('popstate', this.goBack, false);

        }

    },
    destroyed: function () {
        window.removeEventListener('resize', this.chartResize);
        window.removeEventListener('popstate', this.goBack, false);
    },
    methods: {
        changeRow: function (choice) {
            // 获取当前row
            this.currentRow = choice;
            // 更新图表数据
            this.options.dataset[0].source = this.projectTechs[this.currentRow];
            // 更新图表标题
            this.options.title.text = this.currentRow.toUpperCase();
            // 更新图表
            var myChart = echarts.init(document.getElementById('chart-content'));
            myChart.setOption(this.options);
        },
        chartResize: function () {
            var myChart = echarts.getInstanceByDom(document.getElementById('chart-content'));
            myChart.resize();
        },
        calcaluteData: function name(dataJson) {
            let data = dataJson.data;
            // 统计分数对象
            var chartLanguagelist = {};
            var chartFramelist = {};
            for (var i = 0; i < data.length; i++) {
                // vika行
                var item = data[i].fields;
                // 语言统计
                if (!chartLanguagelist[item.language]) {
                    chartLanguagelist[item.language] = {
                        score: 0,
                    };
                }
                chartLanguagelist[item.language].score += item.rate * (item.end_date - item.start_date) / (1000 * 3600 * 24)
                // 框架统计
                if (!chartFramelist[item.framework]) {
                    chartFramelist[item.framework] = {
                        score: 0,
                    };
                }
                chartFramelist[item.framework].score += item.rate * (item.end_date - item.start_date) / (1000 * 3600 * 24)

            }

            // 读语言取对象属性和值
            var chartLanguageData = [];
            for (let key in chartLanguagelist) {
                chartLanguageData.push({
                    name: key,
                    score: chartLanguagelist[key].score
                });
            }
            // 读取框架取对象属性和值
            var chartFrameData = [];
            for (let key in chartFramelist) {
                chartFrameData.push({
                    name: key,
                    score: chartFramelist[key].score
                });
            }
            // 设置数据
            this.projectTechs.language = chartLanguageData;
            this.projectTechs.framework = chartFrameData;

            // 赋值
            this.options.dataset[0].source = chartFrameData;
            var myChart = echarts.getInstanceByDom(document.getElementById('chart-content'));
            myChart.setOption(this.options);
        },
        goBack: function () {
            this.$router.replace({ path: '/home' });
        }
    }
}
</script>

<style lang="scss" scoped>
.capabilities-main {
    width: 100%;

    .header {
        height: 50px;
        padding: 10px 10px 10px 50px;
        position: fixed;
        width: 100%;
        background: white;
        border-color: white;
        box-shadow: 0px 0px 10px #575757;

        .header-bar {
            display: flex;
            flex-direction: row;
            margin-left: 0px;

            img {
                height: 50px;
            }
        }
    }

    .content {
        margin: 75px auto;
        width: 80%;
        text-align: center;

        .chart-set {
            #chart-content {
                width: 100%;
                min-height: 600px;
            }
        }

        .show-type {
            margin-top: 10px;

            span {
                margin: 20px;
                cursor: pointer;
                user-select: none;
                background-color: #f5f5f5;

                // 不可选中
                &:hover {
                    background: #f3efb8;
                }
            }

            .seleted {
                background: #f3efb8 !important;
            }
        }
    }

    @media print {
        .chart-set {
            margin-top: 300px
        }
    }
}
</style>