<template>
  <div class="main stay-center">
    <div class="logo-view stay-center">
      <div>
        <div class="stay-center">
          <div>
            <img class="logo" src="../assets/imgs/logo-red.svg" alt="logo-red" />
          </div>
          <div>
            <span class="name">剣飛</span>
          </div>
        </div>
        <div class="stay-center">
          <img src="../assets/imgs/photo.png" />
        </div>
      </div>
    </div>

    <div class="login-box stay-center">
      <div class="">
        <div>
          Where there is a [&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;], there is a way.
        </div>
        <div>
          <p>{{ enterRegex }}</p>
        </div>
        <div class="stay-center">
          <input type="text" placeholder="Waiting..." :class="{ 'danger-color': answerWrong && entered }"
            v-model="answer" @keydown.enter="junpToHome" />
          <img class="btn_go" src="../assets/imgs/logo-red.svg" alt="Go" @click="junpToHome" />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "WelCome",
  props: {
    msg: String,
  },
  data: function () {
    return {
      enterRegex: /^c[o]{1,1}d(e{1}r)\1$/, // /^s[u]{1,1}(n)\1{1}y$/
      jumpPassword: /^bestseller$/i,
      answer: "",
      entered: false,
    };
  },
  computed: {
    answerWrong: function () {
      return !this.enterRegex.test(this.answer);
    },
  },
  directives: {
    focus: {
      // 指令的定义
      inserted: function (el) {
        el.focus();
      },
    },
  },
  methods: {
    junpToHome: function () {
      this.entered = true;
      if (this.enterRegex.test(this.answer) || this.jumpPassword.test(this.answer)) {
        this.$router.push('/home')
      }
    }
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.main {
  width: 100%;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-style: italic;
  font-size: 24px;

  .logo-view {
    height: 100%;

    .logo {
      width: 165px;
      height: 190px;
    }

    .name {
      font-family: "Helvetica Neue", Helvetica, "PingFang SC",
        "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
      font-size: 72px;
      font-style: normal;
    }

    img {
      width: 70%;
    }
  }

  .login-box {
    height: 100%;

    input {
      font-family: Verdana, Geneva, Tahoma, sans-serif;
      font-style: italic;
      font-size: 20px;
    }

    .danger-color {
      border-color: red;
    }

    .btn_go {
      margin-left: 16px;
      width: 26px;
      height: 30px;
    }
  }

  .stay-center {
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
</style>
