<template>
  <div class="project-container">
    <!-- 公司卡片 -->
    <div v-if="project.company" class="company-card">
      <!-- 公司Header -->
      <div class="company-header">
        <!-- logo -->
        <a :href="project.link" target="_blank">
          <!-- <img :src="project.logo[0].url" alt="LOGO" /> -->
          <img :src="logoUrl" alt="LOGO" />
          <h2>{{ project.company }}</h2>
        </a>
        <span class="work-time">( {{ project.duration }} )</span>
      </div>
      <!-- 公司简介 -->
      <div v-if="project.company_log" class="company-intro">
        <p>
          {{ project.company_log }}
        </p>
        <!-- 开发模型 & 组织结构 -->
        <div v-if="project.organization" class="company-orgs" @click="showPop(organizationUrl)">
          <span v-if="project.organization">组织架构</span>
          <!-- <img :src="project.organization[0].url" alt="org" /> -->
          <img :src="organizationUrl" alt="org" />
        </div>
      </div>
    </div>
    <!-- 项目卡片 -->
    <div class="project-card">
      <!-- 时间轴 -->
      <div class="time-line">
        <div class="icon">
          <img class="" src="@/assets/imgs/logo-red.svg" alt="" />
        </div>
        <div class="time-date">
          <p>{{ project.end_date }}</p>
          <p>~</p>
          <p>{{ project.start_date }}</p>
        </div>
      </div>
      <!-- 项目内容 -->
      <div class="project-content">
        <!-- 项目名称 -->
        <div class="project-name">
          <h3>{{ project.project }}</h3>
        </div>
        <!-- 项目简介 -->
        <div class="project-intro">
          <span>{{ project.project_intro }}</span>
          <img v-if="project.develop_mode" :src="devModeUrl" alt="V-MODE"
            @click="showPop(devModeUrl)">
        </div>
        <!-- 负责事项 -->
        <div v-if="project.my_duty" class="project-duty">
          <h4>负责事项:</h4>
          <ul>
            <li v-for="duty in (project.my_duty.split('\n'))" :key="duty">
              <p>{{ duty }}</p>
            </li>
          </ul>
        </div>
        <!-- 经验积累 -->
        <div v-if="project.highlight" class="project-highlight">
          <h4>经验积累：</h4>
          <ul>
            <li v-for="duty in (project.highlight.split('\n'))" :key="duty">
              <p>{{ duty }}</p>
            </li>
          </ul>
        </div>
        <!-- 使用框架 -->
        <div v-if="project.duty_framework" class="project-frame">
          <h4>使用框架：</h4>
          <div class="frame-content tag-labels">
            <span v-for="item in (project.duty_framework.split('\n'))" :key="item">
              {{ item }}
            </span>
          </div>
        </div>
        <!-- 管理软件 -->
        <div v-if="project.management" class="project-software">
          <h4>管理 & IDE</h4>
          <div class="software-content tag-labels">
            <span v-for="item in (project.management.split('\n'))" :key="item">
              {{ item }}
            </span>
          </div>
          <div class="software-content tag-labels">
            <span v-for="item in (project.ide.split('\n'))" :key="item">
              {{ item }}
            </span>
          </div>
        </div>
      </div>
    </div>
    <div class="pop-content" :class="{ 'pop-hidden': popImgUrl == '' }" @click="closePop">
      <div>
        <img :src="popImgUrl" alt="">
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "ProjectCard",
  props: ["project"],
  data: function () {
    return {
      popImgUrl: "",
    };
  },
  computed: {
    // LogoPath
    logoUrl: function () {
      return require("@/assets/imgs/" + this.project.logoPath);
    },
    // devModePath
    devModeUrl: function () {
      return require("@/assets/imgs/" + this.project.developModePath);
    },
    // organizationPath
    organizationUrl: function () {
      return require("@/assets/imgs/" + this.project.organizationPath);
    }
  },
  methods: {
    showPop: function (url) {
      console.log(url);
      this.popImgUrl = url;
    },
    closePop: function () {
      this.popImgUrl = "";
    },
  },
};
</script>

<style lang="scss" scoped>
.project-container {
  font-family: ui-serif;
  min-width: 300px;

  .company-card {
    box-shadow: 8px -4px 8px #c0c4cc;
    display: flex;
    flex-direction: column;

    .company-header {
      display: flex;
      justify-content: left;
      align-items: center;
      cursor: pointer;
      margin-top: 10px;
      flex-wrap: wrap;

      .work-time {
        margin-left: 20px;
        font-size: 14px;
        color: #515968;
        font-weight: 600;
        font-family: Verdana, Geneva, Tahoma, sans-serif;
      }

      a {
        display: flex;
        flex-wrap: wrap;
        text-decoration: none;

        ::visited,
        ::active {
          color: #2c3e50;
        }

        h2 {
          margin-left: 20px;
          color: #2c3e50;
        }

        img {
          height: 80px;
          float: left;
        }
      }
    }

    .company-intro {
      text-align: left;
      display: flex;
      flex-direction: row;
      align-items: center;

      p {
        display: inline;
        max-width: 1024px;
      }

      .company-orgs {
        float: left;
        display: inline-flex;
        flex-direction: column;
        justify-content: left;
        text-align: center;
        font-size: 12px;
        min-width: 100px;

        img {
          max-width: 200px;
        }
      }
    }

    @media screen and (max-width: 560px) {
      .company-intro {
        flex-direction: column;
        align-items: flex-start;
      }
    }
  }

  .project-card {
    display: flex;
    flex-direction: row;
    margin-top: 20px;
    box-shadow: 14px 0px 8px -6px #c0c4cc;
    padding-bottom: 20px;

    .time-line {
      align-self: stretch;
      border-right: 2px solid #e6e6e6;

      .icon img {
        width: 25px;
        margin-right: 20px;
      }

      .time-date {
        margin: 10px 0px 0px -20px;
        font-size: 12px;
        font-weight: 600;
      }
    }

    .project-content {
      align-self: flex-start;
      text-align: left;
      margin-left: 20px;

      h4 {
        padding-left: 10px;
        border-left: rgb(243, 99, 99) 3px solid;
      }

      .project-intro {
        display: flex;
        flex-direction: column;
        align-items: flex-start;

        span {
          max-width: 1024px;
        }

        img {
          max-height: 120px;
          margin-top: 20px;
        }
      }

      .project-frame {
        .frame-content {
          display: flex;
          flex-direction: row;
          flex-wrap: wrap;

          span {
            // round
            background: #98ffd1;
          }
        }
      }

      .project-software {
        .software-content {
          display: flex;
          flex-direction: row;
          flex-wrap: wrap;

          span {
            background: seashell;
          }
        }
      }
    }
  }

  .pop-hidden {
    display: none !important;
  }

  .pop-content {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;

    div {
      width: 100%;
      height: 100%;
      border-radius: 10px;
      // 居中显示
      display: flex;
      justify-content: center;
      align-items: center;

      img {
        max-width: 100%;
        height: auto;
        background: white;
      }
    }
  }

  img {
    user-select: none;
  }


  // 小屏显示自适应
  @media screen and (max-width: 560px) {

    .company-card,
    .project-card {
      box-shadow: 0px 0px 0px #c0c4cc;
    }

    .company-card,
    .project-card {
      box-shadow: 0px 0px 0px #c0c4cc;
    }
  }

}
</style>